data class Game(
    val name: String,
    val timeOfDay: TimeOfDay,
    val indoorsOutdoors: IndoorsOutdoors,
    val subject: Subject,
    val chores: Chores,
    val taskList: TaskList,
    val length: Length
)

data class GameScore(val game: Game, val score: Float)

enum class TaskList {
    NoList,
    BasicList,
    HintList,
    BuggyList
}

enum class Chores {
    NoChores,
    SomeChores,
    ChoreHeavy,
    LightChores
}

enum class Subject {
    General,
    History,
    Travel,
    ScienceNature
}

enum class IndoorsOutdoors {
    Indoors,
    Outdoors,
    Both
}

enum class TimeOfDay {
    Day,
    Night,
    Both
}

enum class Length {
    Short,
    Medium,
    Long,
    ExtraLong
}

data class Question(val text: String, val answers: List<Answer>)
sealed class Answer(open val text: String) {
    data class TimeOfDayAnswer(override val text: String, val timeOfDay: TimeOfDay) : Answer(text)
    data class IndoorsOutdoorsAnswer(override val text: String, val indoorsOutdoors: IndoorsOutdoors) : Answer(text)
    data class SubjectAnswer(override val text: String, val subject: Subject?) : Answer(text)
    data class ChoresAnswer(override val text: String, val chores: Chores?) : Answer(text)
    data class TasksAnswer(override val text: String, val taskList: TaskList?, val weight: Float = 1.5f) : Answer(text)
    data class LengthAnswer(override val text: String, val length: Length?) : Answer(text)
//    data class CharacterSwitchAnswer(override val text: String, val subject: Subject) : Answer(text)
//    data class OldPersonCluesAnswer(override val text: String, val subject: Subject) : Answer(text)
}
