fun MutableMap<String, GameScore>.incrementEachGameScore(
    selectedAnswer: Answer
) {
    games.forEach { game ->
        val gameScore = this[game.name] ?: return@forEach
        when (selectedAnswer) {
            is Answer.TimeOfDayAnswer -> {
                when (selectedAnswer.timeOfDay) {
                    Day -> when (game.timeOfDay) {
                        Day -> this.incScoreBy(game, 3)
                        Night -> this.incScoreBy(game, 1)
                        Both -> this.incScoreBy(game, 2)
                    }

                    Night -> when (gameScore.game.timeOfDay) {
                        Day -> this.incScoreBy(game, 1)
                        Night -> this.incScoreBy(game, 3)
                        Both -> this.incScoreBy(game, 2)
                    }

                    Both -> Unit
                }
            }

            is Answer.IndoorsOutdoorsAnswer -> {
                when (selectedAnswer.indoorsOutdoors) {
                    Indoors -> when (game.indoorsOutdoors) {
                        Indoors -> this.incScoreBy(game, 3)
                        Outdoors -> this.incScoreBy(game, 1)
                        IndoorsOutdoors.Both -> this.incScoreBy(game, 2)
                    }

                    Outdoors -> when (game.indoorsOutdoors) {
                        Indoors -> this.incScoreBy(game, 1)
                        Outdoors -> this.incScoreBy(game, 3)
                        IndoorsOutdoors.Both -> this.incScoreBy(game, 2)
                    }

                    IndoorsOutdoors.Both -> Unit
                }
            }

            is Answer.SubjectAnswer -> {
                when (selectedAnswer.subject) {
                    General -> when (game.subject) {
                        General -> this.incScoreBy(game, 4)
                        History -> this.incScoreBy(game, 2)
                        Travel -> this.incScoreBy(game, 3)
                        ScienceNature -> this.incScoreBy(game, 1)
                    }

                    History -> when (game.subject) {
                        General -> this.incScoreBy(game, 3)
                        History -> this.incScoreBy(game, 4)
                        Travel -> this.incScoreBy(game, 2)
                        ScienceNature -> this.incScoreBy(game, 1)
                    }

                    Travel -> when (game.subject) {
                        General -> this.incScoreBy(game, 3)
                        History -> this.incScoreBy(game, 2)
                        Travel -> this.incScoreBy(game, 4)
                        ScienceNature -> this.incScoreBy(game, 1)
                    }

                    ScienceNature -> when (game.subject) {
                        General -> this.incScoreBy(game, 3)
                        History -> this.incScoreBy(game, 1)
                        Travel -> this.incScoreBy(game, 2)
                        ScienceNature -> this.incScoreBy(game, 4)
                    }

                    null -> Unit
                }
            }

            is Answer.ChoresAnswer -> {
                when (selectedAnswer.chores) {
                    NoChores -> when (game.chores) {
                        NoChores -> this.incScoreBy(game, 4)
                        SomeChores -> this.incScoreBy(game, 2)
                        ChoreHeavy -> this.incScoreBy(game, 0)
                        LightChores -> this.incScoreBy(game, 3)
                    }

                    SomeChores -> when (game.chores) {
                        NoChores -> this.incScoreBy(game, 2)
                        SomeChores -> this.incScoreBy(game, 4)
                        ChoreHeavy -> this.incScoreBy(game, 2)
                        LightChores -> this.incScoreBy(game, 1)
                    }

                    ChoreHeavy -> when (game.chores) {
                        NoChores -> this.incScoreBy(game, 0)
                        SomeChores -> this.incScoreBy(game, 3)
                        ChoreHeavy -> this.incScoreBy(game, 4)
                        LightChores -> this.incScoreBy(game, 2)
                    }

                    LightChores -> when (game.chores) {
                        NoChores -> this.incScoreBy(game, 3)
                        SomeChores -> this.incScoreBy(game, 4)
                        ChoreHeavy -> this.incScoreBy(game, 1)
                        LightChores -> this.incScoreBy(game, 4)
                    }

                    null -> Unit
                }
            }

            is Answer.LengthAnswer -> {
                when (selectedAnswer.length) {
                    Short -> when (game.length) {
                        Short -> this.incScoreBy(game, 4)
                        Medium -> this.incScoreBy(game, 3)
                        Long -> this.incScoreBy(game, 2)
                        ExtraLong -> this.incScoreBy(game, 1)
                    }

                    Medium -> when (game.length) {
                        Short -> this.incScoreBy(game, 2)
                        Medium -> this.incScoreBy(game, 4)
                        Long -> this.incScoreBy(game, 2)
                        ExtraLong -> this.incScoreBy(game, 1)
                    }

                    Long -> when (game.length) {
                        Short -> this.incScoreBy(game, 1)
                        Medium -> this.incScoreBy(game, 2)
                        Long -> this.incScoreBy(game, 4)
                        ExtraLong -> this.incScoreBy(game, 2)
                    }

                    ExtraLong -> when (game.length) {
                        Short -> this.incScoreBy(game, 1)
                        Medium -> this.incScoreBy(game, 2)
                        Long -> this.incScoreBy(game, 3)
                        ExtraLong -> this.incScoreBy(game, 4)
                    }

                    null -> Unit
                }
            }

            is Answer.TasksAnswer -> {
                when (selectedAnswer.taskList) {
                    BasicList -> when (game.taskList) {
                        NoList -> this.incScoreBy(game, 0, selectedAnswer.weight)
                        BasicList -> this.incScoreBy(game, 3, selectedAnswer.weight)
                        HintList -> this.incScoreBy(game, 3, selectedAnswer.weight)
                        BuggyList -> this.incScoreBy(game, 3, selectedAnswer.weight)
                    }

                    HintList -> when (game.taskList) {
                        NoList -> this.incScoreBy(game, 0, selectedAnswer.weight)
                        BasicList -> this.incScoreBy(game, 2, selectedAnswer.weight)
                        HintList -> this.incScoreBy(game, 3, selectedAnswer.weight)
                        BuggyList -> this.incScoreBy(game, 2, selectedAnswer.weight)
                    }

                    BuggyList, NoList, null -> Unit
                }
            }
        }
    }
}

fun MutableMap<String, GameScore>.incScoreBy(
    game: Game,
    increment: Int,
    weight: Float = 1.0f
) {
    val gameScore = this[game.name] ?: run {
        println("couldnt update score for game ${game.name}")
        return
    }
    this[game.name] = gameScore.copy(score = gameScore.score + increment * weight)
}
