import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.getValue
import androidx.compose.runtime.setValue
import org.jetbrains.compose.web.attributes.InputType
import org.jetbrains.compose.web.attributes.name
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.*
import org.jetbrains.compose.web.renderComposable

fun main() {
    var count: Int by mutableStateOf(0)
    val gamesScoreMap = games.map { GameScore(it, 0f) }.associateBy { it.game.name }
    var gameScoresResult: List<GameScore> by mutableStateOf(listOf())
    val answerMap = mutableMapOf<Question, Answer>()

    renderComposable(rootElementId = "root") {
        questions.forEach { question ->
            Span({ style { padding(15.px) } }) {
                Text(question.text)
            }
            Br { }
            Div {
                question.answers.forEachIndexed { index, answer ->
                    val id = answer.text.replace(" ", "")
                    Input(
                        InputType.Radio,
                        attrs = {
                            name(question.text)
                            value(id.uppercase())
                            id(id)
                            onChange {
                                println("onChange")
                                answerMap[question] = answer
                            }
                        }
                    )
                    Label(forId = id) { Text(answer.text) }
                    Br { }
                }
            }
            Br { }
        }

        Button(attrs = {
            onClick {
                println("onclick")
                count++
                val result = gamesScoreMap.toMutableMap()
                answerMap.forEach { (question, selectedAnswer) ->
                    result.incrementEachGameScore(selectedAnswer)
                }
                gameScoresResult = result.values.sortedByDescending { it.score }
                    .filter { it.game.name != "hated ones" }
                    .subList(0, 5)
            }
        }) {
            Text("Calculate Score")
        }
        Br { }
        Br { }
        Br { }
        gameScoresResult.forEachIndexed { index, gameScore ->
            Span({ style { padding(15.px) } }) {
                Text("${index + 1}. ${gameScore.game.name} (${gameScore.score})")
            }
        }
    }
}

